@import "../../app.scss";

.portfolio {
  position: relative;
  // background-image: url("../../../public/bgport2.png");
  background-repeat: no-repeat ;
  background-size: 100%;
  width: 100%;
  height: 100%;

  .progress {
    position: sticky;
    top: 0;
    left: 0;
    padding-top: 50px;
    text-align: center;
    color: rgb(255, 255, 255);
    font-size: 36px;

    @include mobile {
      padding-top: calc(100vh - 100px);
      font-size: 24px;
    }

    .progressBar {
      height: 10px;
      background-color: white;
    }
    .titleWrapper{
        flex: 1;
        display: flex;
        align-self: flex-end;
        // gap: 20px;
        max-width: 1366px;
        margin: auto;
        
        .titleContainer{
            text-align:left ;
            h2{
                font-size: 70px;
                line-height: 70%;
            }

            .text{
                color: white;
                margin: 1px;
                // position: absolute;
              }
              
              .text2{
                -webkit-text-fill-color: rgba(255, 255, 255, 0);
                -webkit-text-stroke: 0.5px rgb(255, 255, 255);
                // padding-bottom: 5%;
              }
            }
            .subTitleContainer{
                flex: 1;
                align-self: flex-end;
                max-width: 1366px;
                .uiText{
                    font-size: 25px;
                    font-weight: lighter;
                    // text-align: right ;

                }
            }
    }
}


  .container {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    overflow: hidden;

    .wrapper {
      max-width: 1366px;
      height: 100%;
      margin: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      gap: 50px;

      @include mobile {
        flex-direction: column;
      }

      .imageContainer {
        flex: 1;
        height: 50%;

        @include mobile {
          width: 100%;
          max-height: 300px;
        }

        img {
          width: 100%;
          height: 100%;
          // object-fit: cover;
          border-radius: 2%;

          @include mobile {
            object-fit: contain;
          }
        }
      }

      .textContainer {
        flex: 1;
        display: flex;
        flex-direction: column;
        gap: 30px;

        @include mobile {
          transform: none !important;
          padding: 10px;
          align-items: center;
          text-align: center;
        }

        h2 {
          font-size: 50px;
          color: rgb(228, 228, 228);
          @include mobile {
            font-size: 36px;
          }
        }

        p {
          color: rgb(238, 238, 238);
          font-size: 20px;

          @include mobile {
            font-size: 16px;
          }
        }

        button {
          // background-color: orange;
          // border: none;
          // border-radius: 10px;
          // padding: 10px;
          width: 200px;
          // cursor: pointer;
          padding: 20px;
          border: 1px solid white;
          border-radius: 10px;
          background-color: transparent;
          color: white;
          margin-right: 20px;
          cursor: pointer;
          font-weight: 300;
          z-index: 2;
        }
      }
    }
  }
}