@import "../../app.scss";

.parallax {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;


  h2 {
    font-size: 100px;
    z-index: 4;

    @include mobile {
      font-size: 72px;
      text-align: center;
    }
  }

  .mountains {
    background-image: url("../../../public/sakura.png");
    // background-size: cover;
    background-repeat: no-repeat;
    transform: scaleX(1);

    // background-position: bottom;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 3;

    @include mobile {
      background-size: contain;
      background-repeat: no-repeat;
    }
  }
  
  // .planets {
  //   background-size: cover;
  //   background-position: bottom;
  //   width: 100%;
  //   height: 100%;
  //   position: absolute;
  //   z-index: 2;
  //   @include mobile {
  //     background-size: contain;
  //     background-repeat: no-repeat;
  //   }
  // }
  .stars {
    background-image: url("../../../public/petals5.png");
    background-size: cover;
    background-position: bottom;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
  }
}